/** http://meyerweb.com/eric/tools/css/reset/ v2.0 | 20110126   License: none (public domain)**/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/** HTML5 display-role reset for older browsers **/
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
  max-width: 1440px;
}

:root {
  --pink-rgb: 255 96 96;
  --primary-rgb: var(--pink-rgb);
}

html {
  scroll-behavior: smooth;
  overflow-y: scroll;
}

* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  margin: auto;
  padding-left: 55px;
  padding-right: 55px;
  background-color: rgb(255, 255, 255);
  font-family: "Raleway", sans-serif;
}


a {
  text-decoration: none;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

a {
  color: inherit;
}

#Flex {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

@media (max-width: 630px) {
  body {
    font-size: 14px;
    padding-left: 10px;
    padding-right: 10px;
  }
}